


















































































































































export default {
	data() {
		return {
			imgList: [
				'/static/img/im/face/face_1.jpg',
				'/static/img/im/face/face_2.jpg',
				'/static/img/im/face/face_3.jpg',
				'/static/img/im/face/face_4.jpg',
				'/static/img/im/face/face_5.jpg',
				'/static/img/im/face/face_6.jpg',
				'/static/img/im/face/face_7.jpg',
			],
			preferList:[
				{'btntext':'20岁~30岁','isCheck':true},
				{'btntext':'175cm以上','isCheck':true},
				{'btntext':'未婚','isCheck':true},
				{'btntext':'上海','isCheck':true},
				{'btntext':'研究生','isCheck':true},
				{'btntext':'20~30万','isCheck':true},
				{'btntext':'A型血','isCheck':true},
			],
			isShow:false, //遮罩控制
			index: -1,
			picker: ['喵喵喵', '汪汪汪', '哼唧哼唧'],
			multiIndex: [0, 0, 0],
			date1: '请选择',
			date2: '请选择',
			typeIndex: -1,
			typeArr: ['教育类型1', '教育类型2', '教育类型3'],
			multiArray: [
				['北京市', '重庆市'],
				['朝阳区', '海淀区', '通州区'],
				['五道口', '五环街']
			],
			region: ['广东省', '广州市', '海珠区'],
			switchB: false,
			modalName: null,
			textareaAValue: '',
			textareaBValue: ''
		};
	},
	methods: {
		coverShow(){
			this.isShow = true
		},
		coverHide(){
			this.isShow = false
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		
		Date1Change(e) {
			this.date1 = e.detail.value
		},
		Date2Change(e) {
			this.date2 = e.detail.value
		},
		handleEduType(e) { //教育类型
			this.typeIndex = e.detail.value
		},
		
		MultiColumnChange(e) {
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			data.multiIndex[e.detail.column] = e.detail.value;
			switch (e.detail.column) {
				case 0:
					switch (data.multiIndex[0]) {
						case 0:
							data.multiArray[1] = ['朝阳区', '海淀区', '通州区'];
							data.multiArray[2] = ['五道口', '五环街'];
							break;
						case 1:
							data.multiArray[1] = ['渝中区', '渝北区', '九龙坡区'];
							data.multiArray[2] = ['解放碑', '大剧院'];
							break;
					}
					data.multiIndex[1] = 0;
					data.multiIndex[2] = 0;
					break;
				case 1:
					switch (data.multiIndex[0]) {
						case 0:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['1号巷子', '1-1号巷子'];
									break;
								case 1:
									data.multiArray[2] = ['2号巷子'];
									break;
								case 2:
									data.multiArray[2] = ['3号巷子', '3-1号巷子'];
									break;
							}
							break;
						case 1:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['商圈', '步行街'];
									break;
								case 1:
									data.multiArray[2] = ['科技馆', '停车场'];
									break;
								case 2:
									data.multiArray[2] = ['歇台子', '石桥铺'];
									break;
							}
							break;
					}
					data.multiIndex[2] = 0;
					break;
			}
			this.multiArray = data.multiArray;
			this.multiIndex = data.multiIndex;
		},
		
		
		RegionChange(e) {
			this.region = e.detail.value
		},
		SwitchB(e) {
			let that = this;
			uni.showModal({
				confirmColor:'#2DDBC4',
				content:'是否确定绑定支付宝?',
				cancelText:'取消',
				confirmText:'确定',
				cancelColor:'#888',
				success:function(s) {
					console.log(s)
					if(s.confirm){
						that.switchB = true
					}
					if(s.cancel){
						that.switchB = false
					}
					
				},
				fail:function(){
					console.log(2)
					that.switchB = false
				}
			})
			
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			this.imgList.splice(e.currentTarget.dataset.index, 1)
			// uni.showModal({
			// 	title: '召唤师',
			// 	content: '确定要删除这段回忆吗？',
			// 	cancelText: '再看看',
			// 	confirmText: '再见',
			// 	success: res => {
			// 		if (res.confirm) {
			// 			this.imgList.splice(e.currentTarget.dataset.index, 1)
			// 		}
			// 	}
			// })
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		}
	}
}
